:root{--base-color:#4285f4;--bg-color:#f0f0f0;--box-shadow_-_2dp-elevation:0 2px 2px 0 rgba(0, 0, 0, 0.14),0 1px 5px 0 rgba(0, 0, 0, 0.12),0 3px 1px -2px rgba(0, 0, 0, 0.2)}#dashboard--app,body,html{width:100vw;height:100vh;background-color:var(--bg-color);margin:0;padding:0;font:500 14px/150% "Open Sans",sans-serif}.menu-btn::before{content:url(/static/menu.svg);display:inline-block;width:24px;padding-top:8px;box-sizing:border-box}#_-hdr--app{width:100%;height:60px;padding:0 32px;box-sizing:border-box;background:var(--base-color);display:flex;align-items:center;justify-content:space-between;z-index:95}.user-action--notification{height:auto;display:flex}.user-action--notification .notification-btn::before{content:url(/static/notification.svg);display:inline-block;width:24px}.user-action--notification .avatar{width:36px;height:36px;background-color:#333;border-radius:50%}#_-dash-table--forms{width:100%;height:auto;display:flex;justify-content:flex-start;height:calc(100vh - 60px)}.__navigation--table{width:280px;height:calc(100vh - 64px);padding:16px 0;box-sizing:border-box;overflow-y:auto}.__table--form-fields{width:calc(100% - 280px);height:calc(100vh - 60px)}ul{margin:0;padding:0;list-style-type:none;width:100%;height:auto}li{width:90%;height:40px;line-height:40px;margin:0 auto;background-color:transparent;padding:0 32px;box-sizing:border-box;position:relative}li span{position:relative;padding-left:40px}.icon:hover{background-color:#ddd;cursor:pointer;border-radius:3px}.icon::before{content:"";display:inline-block;margin-right:16px;margin-top:calc(20px - 12px);width:24px;height:24px;background-color:#333;position:absolute}.hsT{font-weight:600;font-size:13px}.navitgation-menu_-_stock-list{padding:8px 0;box-sizing:border-box}.navitgation-menu_-_stock-list::after{content:"";width:90%;height:1px;background-color:#ddd;display:inline-block;margin:0 auto;margin-left:5%}#available-stock{width:70%;height:400px;background-color:#fff;margin:32px auto}